@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@700&display=swap);
.movieRow {
  margin-bottom: 30px;
}
.movieRow h2 {
  margin: 0px 0px 0px 30px;
}
.movie-row--listarea {
  overflow-x: hidden;
  padding-left: 30px;
}
.movie-row--list {
  transition: all ease 0.5s;
}
.movieRow--item {
  display: inline-block;
  width: 150px;
  cursor: pointer;
}
.movieRow--item img {
  width: 100%;
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
  transition: all ease 0.2s;
}
.movieRow--item img:hover {
  -webkit-transform: scale(1);
          transform: scale(1);
}
.movie-row--left,
.movie-row--right {
  position: absolute;
  width: 40px;
  height: 225px;
  background-color: rgba(0, 0, 0, 0.6);
  z-index: 99;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  cursor: pointer;
}
.movie-row--left {
  left: 0;
}
.movie-row--right {
  right: 0;
}
.movieRow:hover .movie-row--left,
.movieRow:hover .movie-row--right {
  opacity: 1;
}

@media (max-width: 760px) {
  .movie-row--left,
  .movie-row--right {
    opacity: 1;
  }
}

* {
  box-sizing: border-box;
}

body {
  background-color: black;
  color: white;
  margin: 0;
  font-family: 'Roboto', sans-serif;
}

.lists {
  margin-top: -150px;
}

footer {
  margin: 50px 0;
  text-align: center;
}

.loading {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 99;
  background-color: black;
  display: flex;
  justify-content: center;
  align-items: center;
}

.featured {
  height: 100vh;
}
.featured--vertical {
  width: inherit;
  height: inherit;
  background: linear-gradient(to top, #111 10%, transparent 90%);
}
.featured--horizontal {
  width: inherit;
  height: inherit;
  background: linear-gradient(to right, #111 30%, transparent);
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 30px;
  padding-bottom: 150px;
  padding-top: 70px;
}
.featured--name {
  font-size: 60px;
  font-weight: bold;
}

.featured--info {
  font-size: 18px;
  font-weight: bold;
  margin-top: 15px;
}

.featured--points,
.featured--year,
.featured--seasons {
  display: inline-block;
  margin-top: 15px;
  margin-left: 5px;
}

.featured--points {
  color: aquamarine;
}

.featured--description {
  margin-top: 15px;
  font-size: 20px;
  color: #999;
  max-width: 40%;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 5;
  -webkit-box-orient: vertical;
}

.featured--buttons {
  margin-top: 15px;
}
.featured--watchbutton,
.featured--mylistbutton {
  display: inline-block;
  font-size: 20px;
  font-weight: bold;
  padding: 12px 25px;
  border-radius: 5px;
  text-decoration: none;
  margin-right: 10px;
  opacity: 1;
  transition: all ease 0.2s;
}
.featured--watchbutton,
.featured--mylistbutton {
  opacity: 0.7;
}
.featured--watchbutton {
  background-color: white;
  color: black;
}

.featured--mylistbutton {
  background-color: gray;
  color: whitesmoke;
}

.featured--genres {
  margin-top: 15px;
  font-size: 18px;
  color: gray;
}

@media (max-width: 760px) {
  .featured {
    height: 90vh;
  }
  .featured--name {
    font-size: 16px;
  }
  .featured--info {
    font-size: 16px;
  }

  .featured--description {
    font-size: 14px;
    max-width: 100%;
    margin-right: 30px;
  }
  .featured--buttons,
  .featured--watchbutton {
    font-size: 16px;
  }
  .featured--genres {
    font-size: 14px;
  }
}

header {
  position: fixed;
  z-index: 999;
  top: 0;
  left: 0;
  right: 0;
  height: 70px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 30px;
  background: transparent;
  transition: all ease 0.5s;
}
header.black {
  background-color: black;
}

.header--logo {
  height: 25px;
}
.header--logo img {
  height: 100%;
}

.header--user {
  height: 35px;
}
.header--user img {
  height: 100%;
  border-radius: 3px;
}

